let marryEdges = [
    //婚姻关系
]

//自动获取配偶，关系
nodes.forEach(function (node) {
    let suffix = [
        { source: '姐', target: '姐夫' },
        { source: '妹', target: '妹夫' },
        { source: '哥', target: '嫂' },
        { source: '子', target: '媳' },
        { source: '弟', target: '弟媳' },
        { source: '甥', target: '甥媳' },
        { source: '侄', target: '侄媳' },
        { source: '孙', target: '孙媳' },
        { source: '女', target: '女婿' },
        { source: '女儿', target: '女婿' },
        { source: '姑祖母', target: '姑祖父' },
        { source: '姑母', target: '姑父' },
        { source: '父亲', target: '母亲' },
        { source: '父', target: '母' },
    ]
    for (let i = 0; i < suffix.length; i++) {
        let s = suffix[i]
        if (node.name.endsWith(s.source)) {
            let newItem = JSON.parse(JSON.stringify(node))
            //新增夫妻节点
            newItem.name = newItem.name.replace(s.source, s.target)
            //新增夫妻关系
            let relation = {}
            relation.source = node.name
            relation.target = newItem.name
            if (nodes.filter((node) => node.name == newItem.name).length < 1) {
                //没有这个节点则添加
                nodes.push(newItem)
                marryEdges.push(relation)
            } else {
                //有这个节点但是没有这个关系则添加
                if (marryEdges.filter((edge) => edge.source == node.name && edge.target == newItem.name).length < 1) {
                    marryEdges.push(relation)
                }
            }
            break
        }
    }
})
let basicEdges = raiseEdges.concat(marryEdges)
console.log('节点数：' + nodes.length)
console.log('边数：' + basicEdges.length)
export { nodes, basicEdges }