<template>
  <div id="page-content" class="header-static">
    <div class="container margin-both-sides10">
      <div class="row no-margin">
        <div class="col-md-12 center">
          <form id="contact-form">
            <div class="submit-area padding-onlytop-sm">
              <select class="form-control" v-model="layout" v-show="true">
                <option>d3-force</option>
                <option>cose</option>
                <option>random</option>
                <option>preset</option>
                <option>grid</option>
                <option>circle</option>
                <option>concentric</option>
                <option>breadthfirst</option>
              </select>
              <select class="form-control" v-model="functionType">
                <option value="AB_ROUTE">AB路径</option>
                <option value="SEARCH">称谓查询</option>
                <option value="CALC">关系计算</option>
              </select>
              <div class="inline-block" v-show="functionType == 'AB_ROUTE'">
                从
                <input v-model="a" type="text" placeholder="A"/>
                到
                <input v-model="b" type="text" placeholder="B"/>
                <input
                    @click="ABRoute"
                    id="submit-contact"
                    class="btn-alt active"
                    type="button"
                    value="提交"
                    v-show="false"
                />
              </div>
              <div class="inline-block" v-show="functionType == 'SEARCH'">
                <input v-model="relation" type="text" placeholder="称谓"/>
                <input
                    @click="search"
                    id="submit-contact"
                    class="btn-alt active"
                    type="button"
                    value="查询"
                    v-show="false"
                />
              </div>
              <div class="inline-block" v-show="functionType == 'CALC'">
                <input v-model="relationChain" type="text" placeholder="关系链"/>
                <input
                    @click="calc"
                    id="submit-contact"
                    class="btn-alt active"
                    type="button"
                    value="计算"
                    v-show="false"
                />
              </div>
            </div>
          </form>
        </div>
      </div>

      <div class="row padding-onlybottom-sm" v-show="functionType == 'SEARCH' && relationName">
        <div class="col-md-12">
          <span class="title">名称</span>
          <span class="content" v-text="relationName"></span>
        </div>
      </div>
      <div class="row padding-onlybottom-sm" v-show="functionType == 'SEARCH' && alias">
        <div class="col-md-12">
          <span class="title">别名</span>
          <span class="content" v-text="alias"></span>
        </div>
      </div>
      <div class="row padding-onlybottom-sm" v-show="functionType == 'SEARCH' && foreign">
        <div class="col-md-12">
          <span class="title">外文</span>
          <span class="content" v-text="'英'+ foreign.english && foreign.english"></span>
        </div>
      </div>
      <div class="row padding-onlybottom-sm" v-show="functionType == 'SEARCH' && description">
        <div class="col-md-12">
          <span class="title">介绍</span>
          <span class="content" v-text="description"></span>
        </div>
      </div>
      <div class="row padding-onlybottom-sm" v-show="functionType == 'SEARCH' && level">
        <div class="col-md-12">
          <span class="title">辈分</span>
          <span class="content" v-text="level"></span>
        </div>
      </div>
      <div class="row padding-onlybottom-sm" v-show="functionType == 'SEARCH' && gender">
        <div class="col-md-12">
          <span class="title">性别</span>
          <span class="content" v-text="gender"></span>
        </div>
      </div>
      <div class="row padding-onlybottom-sm" v-show="pathDesc">
        <div class="col-md-12">
          <span class="title">最短关系链：</span>
          <span class="content" v-text="pathDesc"></span>
        </div>
      </div>
      <div class="row padding-onlybottom-sm" v-show="functionType == 'CALC' && calcRelation">
        <div class="col-md-12">
          <span class="title">我应该叫他：</span>
          <span class="content" v-text="displayCalcRelation"></span>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <div
              ref="mainChart"
              style="display:inline-block; margin:auto; width: 100%; height:calc(100vh);border:1px solid lightskyblue"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import cytoscape from 'cytoscape'

import {nodes, basicEdges} from '../components/kinshiplib.js'
import d3Force from 'cytoscape-d3-force'

cytoscape.use(d3Force)
let elements = []
let id = 0
let getGender = function (name) {
  let manEnds = [
    '父',
    '哥',
    '弟',
    '甥',
    '婿',
    '舅',
    '孙',
    '子',
    '夫',
    '侄',
    '父亲',
    '兄',
  ]
  let womanEnds = ['母', '姐', '妹', '女', '妻', '媳', '嫂', '女儿', '母亲']

  if (
      manEnds.filter((suffix) => {
        return name.endsWith(suffix)
      }).length > 0
  ) {
    return 1
  }

  if (
      womanEnds.filter((suffix) => {
        return name.endsWith(suffix)
      }).length > 0
  ) {
    return 0
  }

  return ''
}
nodes.forEach(function (item) {
  let element = {}
  element.data = item
  item.gender = getGender(item.name)
  if (!item.foreign) {
    item.foreign = {}
  }
  if (item.gender === 0) {
    element.style = {
      'background-color': '#E989B9',
    }
  } else if (item.gender === 1) {
    element.style = {
      'background-color': '#05A8F5',
    }
  }
  element.data.id = '' + id++
  elements.push(element)
})
let searchNode = function (name) {
  let results = elements.filter(function (item) {
    return (
        item.data.name == name ||
        (item.data.alias && item.data.alias.includes(name))
    )
  })
  if(results.length<1){
    console.log(name+'不存在')
  }
  return results[0]
}
basicEdges.forEach(function (item) {
  let element = {}
  element.data = {
    source: searchNode(item.source).data.id,
    target: searchNode(item.target).data.id,
  }
  if (
      searchNode(item.source).data.level == searchNode(item.target).data.level
  ) {
    //夫妻关系
    element.style = {
      'line-color': '#E989B9',
      'target-arrow-color': '#E989B9',
    }
  } else {
    //抚养关系
    element.style = {
      'line-color': '#ccc',
      'target-arrow-color': '#ccc',
    }
  }
  elements.push(element)
})
let cy = {}
export default {
  data: function () {
    return {
      layout: 'cose',
      functionType: 'AB_ROUTE',
      pathDesc: '', //结果显示，关系链
      relation: '', //用户输入，关系查询
      relationName: '', //结果显示，关系名
      alias: '', //结果显示，别名
      description: '', //结果显示，描述
      a: '', //用户输入，起点
      b: '', //用户输入，终点
      calcRelation: [], //我应该叫他,这是一个数组因为可能有多个结果
      relationChain: '', //用户输入，关系链
      foreign: '', //结果显示，外文名
      level: '', //结果显示，辈分
      gender: '', //结果显示，性别
    }
  },
  computed: {
    layoutOption: function () {
      let layoutOption = {}
      layoutOption.name = this.layout
      if (this.layout == 'd3-force') {
        layoutOption.animate = true
        layoutOption.fit = false
        layoutOption.fixedAfterDragging = false
        layoutOption.linkDistance = 150
        layoutOption.manyBodyStrength = -600
        layoutOption.randomize = false
        layoutOption.infinite = true
        layoutOption.linkId = function(d){
          return d.id
        }
      }
      return layoutOption
    },
    displayCalcRelation: function () {
      if(this.calcRelation.length<1){
        return '未知的称谓'
      }
      return this.calcRelation.join(' 或 ')
    }
  },
  mounted: function () {
    cy = cytoscape({
      container: this.$refs.mainChart,
      elements: elements,
      style: [
        {
          selector: 'node',
          style: {
            'background-color': '#666',
            label: 'data(name)',
            'overlay-opacity':0
          },
        },
        {
          selector: 'edge',
          style: {
            'target-arrow-shape': 'triangle',
            'curve-style': 'bezier',
          },
        },
        {
          selector: '.light-off',
          style: {
            opacity: 0.15,
          },
        },
      ],
      layout: this.layoutOption,
    })
  },
  watch: {
    layout: function () {
      let layout = cy.layout(this.layoutOption)
      layout.run()
    },
    a: function () {
      this.ABRoute()
    },
    b: function () {
      this.ABRoute()
    },
    relation: function () {
      this.search()
    },
    relationChain: function () {
      this.calc()
    },
  },
  methods: {
    clearSearchResults: function () {
      this.relationName = ''
      this.alias = ''
      this.description = ''
      this.level = ''
      this.gender = ''
      this.pathDesc = ''
      this.foreign = ''
    },
    /**
     * 所有节点恢复高亮
     */
    lightsOn: function () {
      cy.elements().removeClass('light-off')
    },
    /**
     * 所有节点变暗
     */
    lightsOff: function () {
      cy.elements().addClass('light-off')
    },
    /**
     * 将路径转化为基本称谓数组
     * @param path
     * @returns {*[]}
     */
    describePath: function (path) {
      let prev = null
      let pathDesc = []
      for (let i = 0; i < path.length; i++) {
        let current = path[i].data()
        let nextNode = path[parseInt(i) + 1]
        if (!nextNode) {
          continue
        }
        let next = nextNode.data()
        if (!prev) {
          pathDesc.push(current.name)
        } else {
          if (prev.hasOwnProperty('name')) {
            //前者是节点
            if (prev.level > next.level) {
              if (next.gender == '1') {
                pathDesc.push('儿子')
              } else {
                pathDesc.push('女儿')
              }
            } else if (prev.level == next.level) {
              if (next.gender == '1') {
                pathDesc.push('丈夫')
              } else {
                pathDesc.push('妻子')
              }
            } else {
              if (next.gender == '1') {
                pathDesc.push('父亲')
              } else {
                pathDesc.push('母亲')
              }
            }
          }
        }
        prev = current
      }
      return pathDesc
    },
    /**
     * 将复合称谓拆分成基本称谓
     * @param complexRelation
     * @returns {*[]}
     */

    explodeRelation: function (complexRelation) {
      let source = searchNode('我')
      let target = searchNode(complexRelation)
      if (target) {
        let aStar = cy
            .elements()
            .aStar({root: '#' + source.data.id, goal: '#' + target.data.id})
        let pathDesc = this.describePath(aStar.path)
        return pathDesc
      }
    },
    /**
     * 计算从A到B的路径并显示在屏幕上
     * @constructor
     */
    ABRoute: function () {
      if (!this.a || !this.b) {
        this.lightsOn()
        this.pathDesc = ''
        return
      }
      let source = searchNode(this.a)
      let target = searchNode(this.b)
      if (!source || !target) {
        this.lightsOn()
        this.pathDesc = '未知的起源或目标'
        return
      }
      let aStar = cy
          .elements()
          .aStar({root: '#' + source.data.id, goal: '#' + target.data.id})
      let path = aStar.path
      path.select()
      this.lightsOff()
      path.removeClass('light-off')
      cy.fit(path)
      let pathDesc = this.describePath(path)
      this.pathDesc = pathDesc.join('的')
    },
    /**
     * 查找目标称谓显示在屏幕上并且计算和我的关系
     */
    search: function () {
      let source = searchNode(this.relation)
      if (!source) {
        this.clearSearchResults()
        this.relationName = '未知的称谓'
        this.lightsOn()
        return
      }
      this.relationName = this.relation
      this.alias = source.data.alias && source.data.alias.join(', ')
      this.description = source.data.description
      if (source.data.level == 0) {
        this.level = '平辈'
      } else if (source.data.level > 0) {
        this.level = '+' + source.data.level
      } else {
        this.level = source.data.level
      }
      this.gender = source.data.gender == 0 ? '女' : '男'
      this.a = '我'
      this.b = this.relationName
      this.foreign = source.data.foreign
      this.ABRoute()
    },
    /**
     * 根据关系链计算目标并显示在屏幕上
     */
    calc: function () {
      if (!this.relationChain) {
        //没有任何输入还原初始状态
        this.lightsOn()
        return
      }
      let basicRelation = ['我', '父亲', '母亲', '儿子', '女儿', '妻子', '丈夫']
      //输入按的字拆分
      let splitChain = this.relationChain.split('的')
      //第一个作为起点不需要解析一会直接显示即可
      let explodedChain = []
      //检查输入是否有复合称谓
      for (let i = 0; i < splitChain.length; i++) {
        if(!splitChain[i]){
          return;
        }
        let source = searchNode(splitChain[i])
        if (!source) {
          this.calcRelation = []
          return;
        }
        if (i == 0 || basicRelation.includes(source.data.name)) {
          explodedChain.push(splitChain[i])
        } else {
          //有复合称谓的拆分成基本称谓
          explodedChain = explodedChain.concat(this.explodeRelation(splitChain[i]).slice(1))
        }
      }
      //Start the show
      this.lightsOff()
      let currentNodes = [];
      //遍历基本称谓链
      for (let i = 0; i < explodedChain.length; i++) {
        let source = searchNode(explodedChain[i])
        if (i == 0) {
          //对于起始节点
          let startNode = cy.$('#' + source.data.id);
          cy.center(startNode)
          //起始节点，直接点亮，不做查找操作
          startNode.removeClass('light-off')
          currentNodes.push(startNode)
          continue
        }

        //对于每一个找到的节点, 都需要解析出一条路径
        let tempNodes = []//解析结果
        for (let j = 0; j < currentNodes.length; j++) {
          let currentNode = currentNodes[j];
          let neighborhood = currentNode.neighborhood()
          //需要找到currentNode的爸爸/妈妈/女儿/儿子/妻子/丈夫是哪个节点
          let subNodes = this.findByRelation(currentNode, neighborhood, explodedChain[i])
          if (!subNodes || subNodes.length < 1) {
            //如果没有找到
            continue
          }
          subNodes.forEach((item) => {
            item.removeClass('light-off')
            //找到非边的对象
            if (item.data().name) {
              //可能有重复，需要去重
              if (tempNodes.filter((tempNode) => tempNode.data().name == item.data().name).length < 1) {
                tempNodes.push(item)
              }
              cy.center(currentNodes)
            }
          })
        }
        currentNodes = tempNodes
      }
      this.calcRelation = currentNodes.map((item) => item.data().name)
    },
    /**
     * 在node的邻居节点中找到基本节点描述的节点
     * @param node 本节点
     * @param neighborhood 本节点的邻居
     * @param relation 基本称谓之一
     * @returns {null|*}
     */
    findByRelation: function (node, neighborhood, relation) {
      let source = searchNode(relation)
      //对于每一个相邻节点来说
      let result = []
      if (source.data.name == '父亲') {
        result = neighborhood.filter((item) => {
          return item.data().gender == '1' && item.data().level == node.data().level + 1
        })
      } else if (source.data.name == '母亲') {
        result = neighborhood.filter((item) => {
          return item.data().gender == '0' && item.data().level == node.data().level + 1
        })
      } else if (source.data.name == '儿子') {
        result = neighborhood.filter((item) => {
          return item.data().gender == '1' && item.data().level == node.data().level - 1
        })
      } else if (source.data.name == '女儿') {
        result = neighborhood.filter((item) => {
          return item.data().gender == '0' && item.data().level == node.data().level - 1
        })
      } else if (source.data.name == '妻子') {
        result = neighborhood.filter((item) => {
          return item.data().gender == '0' && item.data().level == node.data().level
        })
      } else if (source.data.name == '丈夫') {
        result = neighborhood.filter((item) => {
          return item.data().gender == '1' && item.data().level == node.data().level
        })
      }
      //把边也返回
      let edges = neighborhood.filter((item) => {
        return result.map((node) => node.data().id).includes(item.data().source) || result.map((node) => node.data().id).includes(item.data().target)
      })
      edges.forEach((edge) => {
        result.push(edge)
      })
      return result;
    }
  },
}
</script>
<style scoped>
.form-control {
  display: inline;
  width: 100px;
  height: 40px;
  margin-right: 5px;
}

.title {
  font-weight: bold;
  padding-right: 10px;
  color: var(--main-color);
}

.content {
  font-weight: bold;
}
</style>
